.content {
  padding: 41px 20px;
  @include mq(desktop) {
    margin-left: 306px;
    padding: 41px 95px 41px 71px;
  }
}

.content__header {
  margin-bottom: 50px;
  display: flex;
  align-items: center;
}

.content__container {
  padding: 30px 40px 40px 38px;
  border-radius: 30px;
  background-color: $primary-white-color;
  box-shadow: 0 10px 60px 0 $primary-shadow-color;
}

.content__head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.content__title-wrap {
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.content__title {
  font-weight: 600;
  font-size: 22px;
}

.content__subtitle {
  color: $accent-text-color;
}

.content__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
}
